* {
  font-size: 1.1em;
}


.content {
  padding: .5em;
}

.App h1 {
  font-size: 3em;
  margin: 0;
  padding: 1.5em .5em;
}

h2 {
  font-size: 2em;
}

#pag-inicial {
  text-align: center;
  color: white;
  background-image: url("/img/s2ufc.jpg");

  /* Set a specific height */
  height: 100vh;

  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

#pag-inicial #front {
  height: 100%; 
  background-color: rgba(0, 0, 0, .60);
}

#discente-info {
  text-align: center;

  .icon {
    font-size: 2em;
  }

  .discente-info-item {
    display: inline-block;
  }

  .arrow {
    padding: 0 2em;
    vertical-align: super;
  }
}